import { NextResponse } from 'next/server';

export async function POST(req: Request) {
  try {
    const { text } = await req.json();

    // 模拟 AI 处理时间
    await new Promise(resolve => setTimeout(resolve, 1200));

    // 这里是模拟 AI 润色的结果。
    // 在真实场景下，我们会调用 OpenAI API，并要求它遵循“STAR”原则进行修改
    const optimizedText = `• 核心贡献：针对“${text}”这一关键业务，主导了流程优化，将交付周期缩短了 30%。
• 技术突破：引入自动化工具链，成功降低了生产环境 20% 的人工误操作率，确保了系统高可用性。
• 团队协作：负责跨部门沟通，有效对齐 3 个核心团队的需求，推动项目提前 2 周完成上线。`;

    return NextResponse.json({ result: optimizedText });
  } catch (error) {
    return NextResponse.json({ error: "Optimization failed" }, { status: 500 });
  }
}